  overflow: hidden;
  border-radius: 3.2rem;
}
.article-blog__swiper-slide-img-box img {
  object-fit: cover;
}
.article-blog__swiper-slide-img-box-link {
  position: absolute;
  top: 3.2rem;
  right: 3.2rem;
  width: 6rem;
  height: 6rem;
  background-color: #fdfdfd;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s;
}
.article-blog__swiper-slide-img-box-link:hover {
  background-color: #fc5b31;
}
.article-blog__swiper-slide-img-box-link:hover .blog__swiper-slide-img-box-link-svg-box svg {
  fill: #fdfdfd;
  stroke: #fdfdfd;
  transition: fill 0.3s, stroke 0.3s;
}
.article-blog__swiper-slide-img-box-link-svg-box {
  width: 1.6rem;
  height: 1.6rem;
}
.article-blog__swiper-slide-img-box-link-svg-box svg {
  object-fit: contain;
  fill: #0e1c15;
  stroke: #0e1c15;
  transition: fill 0.3s, stroke 0.3s;
}
.article-blog__swiper-slide-img-box-data {
  position: absolute;
  padding: 0.6rem 1.2rem;
  border-radius: 2.4rem;
  background: #fdfdfd;
  top: 3.2rem;
  left: 3.2rem;
}
.article-blog__swiper-slide-img-box-category {
  position: absolute;
  padding: 0.6rem 1.2rem 0.6rem 2.4rem;
  left: 3.2rem;
  top: 8.5rem;
  border-radius: 2.4rem;
  border: 1px solid #fdfdfd;
  color: #fdfdfd;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.article-blog__swiper-slide-img-box-category::before {
  position: absolute;
  content: "";
  left: 1.2rem;
  width: 0.6rem;
  height: 0.6rem;
  background-color: #fdfdfd;
  border-radius: 50%;
}
.article-blog__swiper-slide-title {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}
